<template>
  <div class="template-container">
    <div class="template-item-wrap">
      <!-- 时间选择器 begin -->
      <!-- <div class="temp-item-inlineblock">
        <el-date-picker v-model="value7" type="daterange" align="left" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
        </el-date-picker>
      </div> -->
      <!-- 时间选择器 end  -->
      <!-- <div class="temp-item-inlineblock">
        <el-select v-model="value" placeholder="请选择">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div> -->
      <div class="temp-item-inlineblock">
        <el-select v-model="searchParams.product_type" placeholder="标的物类型">
          <el-option v-for="item in productTypeList" :key="item.value" :label="item.name" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-input placeholder="创意 ID" v-model="searchParams.adcreative_id"></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-input placeholder="创意名称" v-model="searchParams.adcreative_name"></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="getList">查询</el-button>
      </div>
      <!-- <div class="temp-item-inlineblock">
        <el-button type="primary" >导出为excel</el-button>
      </div> -->
      <div class="temp-item-inlineblock temp-item-right">
        <el-button type="primary" @click="newAdCreative">新建广告创意</el-button>
      </div>

      <div class="temp-item-block">
        <el-button type="primary" @click="handleListRemove">批量删除</el-button>
      </div>
    </div>
    <div class="table-wrap" v-loading="loading">
      <el-table show-overflow-tooltip ref="multipleTable" @selection-change="handleSelectionChange" :data="adcreativesList" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column prop="adcreative_id" label="ID" sortable>
        </el-table-column>
        <el-table-column prop="adcreative_name" label="创意名称" sortable>
        </el-table-column>
        <el-table-column prop="campaign_id" label="推广计划">
        </el-table-column>
        <el-table-column prop="product_type_name" label="标的物类型">
        </el-table-column>
        <el-table-column key="adcreative_elements" label="预览图">
          <template slot-scope="scope"> 
            <div class="show-tuwen"> 
              <img :src="scope.row.adcreative_elements.image_url" style="max-height: 200px; height: auto; width:100%;" />
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="destination_url" label="落地页">
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="handleEditClick(scope.row.adcreative_id)" type="text" size="small">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="table-pagination" v-if="page_info.total_number>0">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_info.page" :page-sizes="[10, 20, 30, 40]" :page-size="page_info.page_size" layout="total, sizes, prev, pager, next, jumper" :total="page_info.total_number">
        </el-pagination>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  created: function() {
    if (this.account_id) {
      this.getAdcreatives(this.searchParams)
    }
    this.getCommonEnum()
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.getAdcreatives(this.searchParams)
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      multipleSelection: [], // 批量选择
      loading: false,
      adcreativesList: [],
      productTypeList: [
        {
          value: '',
          name: '标的物类型'
        }
      ],
      searchParams: {
        adcreative_id: '',
        adcreative_name: '',
        product_type: '',
        page: 1,
        page_size: 10,
        start_date: '',
        end_date: '',
        show_image: 1
      },
      page_info: {
        page: 1,
        page_size: 10,
        total_number: 1,
        total_page: 1
      },
      pickerOptions2: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      value6: '',
      value7: '',
      tableData: [
        {
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        },
        {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄'
        }
      ]
    }
  },
  methods: {
    // 批量选择
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    // 批量删除
    handleListRemove() {
      var that = this,
        rows = this.multipleSelection,
        ids = []
      if (rows && rows.length > 0) {
        for (var i in rows) {
          ids.push(rows[i]['adcreative_id'])
        }
        this.$confirm('是否确认批量删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            var params = {
              ids: ids
            }
            that.handleListRemoveRequest(params) // 批量删除请求
          })
          .catch(() => {})
      } else {
        that.$message('请选择需要删除的记录')
      }
    },
    // 批量删除请求
    handleListRemoveRequest(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adcreatives/batchDelete', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          that.$message({
            type: 'success',
            message: respData.msg
          })
          if (respData.code == 0) {
            setTimeout(function() {
              that.getAdcreatives(that.searchParams) // 刷新广告创意列表
            }, 500)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    // 创意编辑
    handleEditClick(id) {
      this.$router.push('/advertising/adCreative/edit/' + id)
    },

    // 分页
    handleSizeChange(val) {
      this.searchParams.page_size = val
      this.getAdcreatives(this.searchParams)
      //console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      this.searchParams.page = val
      this.getAdcreatives(this.searchParams)
      //console.log(`当前页: ${val}`)
    },
    // 新建
    newAdCreative() {
      this.$router.push('/advertising/adCreative/new')
    },
    // 查询
    getList() {
      this.getAdcreatives(this.searchParams)
    },
    // 获取列表
    getAdcreatives(params) {
      var that = this
      that.loading = true
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adcreatives/get', {
          params: params
        })
        .then(function(response) {
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list || respData.data.page_info) {
              that.adcreativesList = respData.data.list
              that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loading = false
        })
    },
    getCommonEnum() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            var product_type = respData.data['PRODUCT_TYPE']
            for (var key in product_type) {
              var obj = new Object()
              obj.value = key
              obj.name = product_type[key]['name']
              that.productTypeList.push(obj)
            }
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    }
  }
}
</script>


<style scoped>

</style>



